import { useForm } from "react-hook-form";
import css from "./searchForm.css";

const SearchForm = () => {
    const { register, formState: { errors }, handleSubmit } = useForm();
    const onSubmit = (data) => console.log(data);

    const errorSearchFormStyle = {
        fontSize: '10px',
        color: '#FF5959',
        paddingTop: '6px',
        marginLeft: '20px'
    };

    return(
        <>
            <form onSubmit={handleSubmit(onSubmit)} className="search_wrapper">
                <div className="search_wrapper_top">
                    <div className="search_wrapper_inputs">

                        <h3 className='search_form_text'> *</h3>
                        <input className="search_form_input"
                            {...register("company", { required: true, minLength: 10, maxLength: 10 })} 
                            aria-invalid={errors.number ? "true" : "false"} 
                            type='number'
                            placeholder="10 "
                        />
                        <div className='search_errors_form_string'>
                            {errors.company?.type === 'required' && <div style={errorSearchFormStyle} role="alert">  </div>}
                            {errors.company?.type === 'minLength' && <div style={errorSearchFormStyle} role="alert">  </div>}
                            {errors.company?.type === 'maxLength' && <div style={errorSearchFormStyle} role="alert">  </div>}
                        </div>

                        <h3 className='search_form_text'></h3>
                        <select className="search_form_input"
                            {...register("category", { required: true })}>
                            <option value=""></option>
                            <option value="A"></option>
                            <option value="B"></option>
                        </select>
                        <div className='search_errors_form_string'></div>

                        <h3 className='search_form_text'>   *</h3>
                        <input className="search_form_input"
                            {...register("number", { required: true, minLength: 1, maxLength: 1000 })} 
                            aria-invalid={errors.number ? "true" : "false"} 
                            type='number'
                            placeholder=" 1  1000"
                        />
                        <div className='search_errors_form_string'>
                            {errors.number?.type === 'required' && <div style={errorSearchFormStyle} role="alert"> </div>}
                            {errors.number?.type === 'minLength' && <div style={errorSearchFormStyle} role="alert"> </div>}
                            {errors.number?.type === 'maxLength' && <div style={errorSearchFormStyle} role="alert"> </div>}
                        </div>

                        <h3 className='search_form_text'> *</h3>

                    </div>

                    <div className="search_wrapper_checkboxs">
                        <div className="search_checkbox_box"><input type="checkbox" /><label className="search_checkbox_text" >  </label></div>
                        <div className="search_checkbox_box"><input type="checkbox" /><label className="search_checkbox_text" >  -</label></div>
                        <div className="search_checkbox_box"><input type="checkbox" /><label className="search_checkbox_text" >   </label></div>
                        <div className="search_checkbox_box"><input type="checkbox" /><label className="search_checkbox_text" >   -</label></div>
                        <div className="search_checkbox_box"><input type="checkbox" /><label className="search_checkbox_text" >   </label></div>
                        <div className="search_checkbox_box"><input type="checkbox" /><label className="search_checkbox_text" >   </label></div>
                        <div className="search_checkbox_box"><input type="checkbox" /><label className="search_checkbox_text" >  </label></div>
                    </div>
                </div>

                <div className="search_data_container">
                    <div className="search_data_container_inputs">
                        <input className="search_form_data_input"
                        {...register("date", { required: true })} 
                        aria-invalid={errors.data ? "true" : "false"} 
                        type='date'
                        placeholder=" "
                        />

                        <input className="search_form_data_input"
                        {...register("date", { required: true })} 
                        aria-invalid={errors.data ? "true" : "false"} 
                        type='date'
                        placeholder=" "
                        />
                    </div>

                    <button className="search_form_data_button" type="submit"></button>
                </div>
            </form>
        </>
    )
}

export default SearchForm